import { useDispatch, useSelector } from "react-redux";
import { useState } from "react";

import * as Tone from "tone";

import { setBpm, setTacts } from "../../redux/slices/settingsSlice";

import { resizeArray } from "../../redux/slices/melodyArraySlice";

import startIcom from "./images/play_icon.png";
import pauseIcon from "./images/pause_icon.png";
import stopIcon from "./images/stop_icon.png";

import "./header.scss";
import { setBit } from "../../redux/slices/bitSlice";

function Header({ className = "" }) {
  const [myBpm, setMyBpm] = useState(120);
  const [myTacts, setMyTacts] = useState(4);

  const settings = useSelector((state) => state.settings);

  const dispatch = useDispatch();

  return (
    <header className={`header ${className}`}>
      <div className="header__buttons">
        <button
          className="header__button"
          onClick={() => {
            if (Tone.context.state === "suspended") {
              Tone.context.resume();
            }
            Tone.Transport.start();
          }}
        >
          <img
            className="header__icon header__icon_start"
            src={startIcom}
            alt="start"
          />
        </button>

        <button
          className="header__button"
          onClick={() => {
            Tone.Transport.pause();
          }}
        >
          <img
            className="header__icon header__icon_pause"
            src={pauseIcon}
            alt="pause"
          />
        </button>

        <button
          className="header__button"
          onClick={() => {
            Tone.Transport.stop();
            dispatch(setBit(0));
          }}
        >
          <img
            className="header__icon header__icon_stop"
            src={stopIcon}
            alt="stop"
          />
        </button>
      </div>
      <form
        onSubmit={(event) => {
          event.preventDefault();
          dispatch(setBpm(myBpm));
        }}
      >
        bpm
        <input
          className="header__input"
          type="text"
          defaultValue={settings.bpm}
          onChange={(event) => {
            setMyBpm(event.target.value);
          }}
        ></input>
      </form>
      <form
        onSubmit={(event) => {
          event.preventDefault();
          dispatch(setTacts(myTacts));
          dispatch(resizeArray(myTacts));
        }}
      >
        tacts
        <input
          className="header__input"
          type="text"
          defaultValue={settings.tacts}
          onChange={(event) => {
            setMyTacts(event.target.value);
          }}
        ></input>
      </form>
    </header>
  );
}

export default Header;
