import { useSelector } from 'react-redux';

import * as Tone from 'tone'
import AudioKeys from 'audiokeys';

import store from './redux/store/store';
import { setBit } from './redux/slices/bitSlice';

/* creating a synth */
const synth = new Tone.PolySynth(Tone.Synth, {
  oscillator: {
    type: "sine"
  },
  envelope: {
    attack: 0,
    decay: 0,
    sustain: 1,
    release: 0
  }
});

/* creating effects */

//tremoloFrequency [0, 10]
//tremoloDepth [0, 1]
const tremolo = new Tone.Tremolo(0, 0).start();

//delayTime [0, 1]
//feedback [0, 1]
const delay = new Tone.FeedbackDelay(0, 0);

//distortion [0, 10]
const dist = new Tone.Distortion(0);

//bits [1, 16]
const crusher = new Tone.BitCrusher(16);

//pitchShift [0, 1]
const shifter = new Tone.PitchShift(0).toDestination();

synth.chain(crusher, delay, dist, shifter, tremolo, Tone.Destination);

/* creating a loop music */
new Tone.Loop(playMusic, "4n").start();

function playMusic(time) {
  const bit = Tone.Transport.position.split(":");
  const currentBit = (parseInt(bit[0]) * 4 + parseInt(bit[1])) % (store.getState().settings.tacts * 4);
  store.dispatch(setBit(currentBit));
  const dict = store.getState().melodyArray.melodyArray;
  for(var key in dict) {
    if (dict[key][currentBit]) {
      synth.triggerAttackRelease(key, "4n", time);
    }
  }
};


/* creating a keyboard event */
const keyboard = new AudioKeys({
  rows: 1
});

keyboard.down((key) => {
  synth.triggerAttack(key.frequency);
})

keyboard.up((key) => {
  synth.triggerRelease(key.frequency);
});

/* creating a component */
function SoundManager() {
  const soundSettings = useSelector((state) => state.soundSettings);
  const bpm = useSelector((state) => state.settings.bpm);

  synth.volume.value = soundSettings.volume;
  synth.set({
    oscillator: {
      type: soundSettings.wave
    },
    envelope : {
      attack : soundSettings.attack,
      decay : soundSettings.decay,
      sustain : soundSettings.sustain,
      release : soundSettings.release
    }
  });

  tremolo.frequency.value = soundSettings.tremoloFrequency;
  tremolo.depth.value = soundSettings.tremoloDepth;
  delay.delayTime.value = soundSettings.delayTime;
  delay.feedback.value = soundSettings.feedback;
  dist.distortion = soundSettings.distortion;
  crusher.bits.value = soundSettings.bits;
  shifter.pitch = soundSettings.pitchShift;
  
  Tone.Transport.bpm.value = bpm;
}

export default SoundManager;
