import { useDispatch, useSelector } from "react-redux";

import Note from "../Note/Note";
import { changeValue } from "../../redux/slices/melodyArraySlice";

import "./bit.scss";

function Bit({ className = "", bitNumber }) {
  const melodyArray = useSelector((state) => state.melodyArray.melodyArray);
  const currentBit = useSelector((state) => state.bit.bit);

  const dispatch = useDispatch();

  return (
    <div
      className={`bit ${bitNumber === currentBit && "bit--active"} ${className}`}
    >
      {Object.entries(melodyArray).map((element) => {
        return (
          <Note
            key={element}
            className={element[0].charAt(1) === "#" && "note-button--gray"}
            isActive={melodyArray[element[0]][bitNumber]}
            onClick={async () => {
              dispatch(
                changeValue([
                  element[0],
                  bitNumber,
                  !melodyArray[element[0]][bitNumber],
                ])
              );
            }}
          />
        );
      })}
    </div>
  );
}

export default Bit;
