import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  bpm: 120,
  tacts: 8
};

export const settingsSlice = createSlice({
  name: 'settings',
  initialState,
  reducers: {
    setBpm: (state, action) => {
      state.bpm = action.payload;
    },
    setTacts: (state, action) => {
      state.tacts = action.payload;
    }
  }
});
export const { setBpm, setTacts } = settingsSlice.actions;
