import { useSelector } from "react-redux";

import "./noteNames.scss"

function NoteNames({ className = "" }) {
  const melodyArray = useSelector((state) => state.melodyArray.melodyArray);

  return (
    <div className={`note-names ${className}`}>
      {Object.entries(melodyArray).map((element) => {
        return <p className="note-names__element" key={ element }>{element[0]}</p>
      })}
    </div>
  );
}

export default NoteNames;
  