import { useSelector } from "react-redux";

import Tact from "../Tact/Tact";
import NoteNames from "../NoteNames/NoteNames";

import "./melody.scss"

function Melody({ className = "" }) {
  const tactsCounter = useSelector((state) => state.settings.tacts);

  var tacts = [];
  for (var i = 0; i < tactsCounter; i++) {
    tacts.push(<Tact key={i} tactNumber={i}/>);
  }

  return (
    <div className={`melody ${className}`}>
      <NoteNames />
      {tacts}
    </div>
  );
}

export default Melody;
  