import { createSlice } from "@reduxjs/toolkit";

const initialState = {
  volume: 0,
  attack: 0,
  decay: 0,
  sustain: 1,
  release: 0,
  tremoloFrequency: 0,
  tremoloDepth: 0,
  delayTime: 0,
  feedback: 0,
  distortion: 0,
  bits: 16,
  pitchShift: 0,
  wave: "sine",
};

export const soundSettingsSlice = createSlice({
  name: "soundSettings",
  initialState,
  reducers: {
    setVolume: (state, action) => {
      state.volume = action.payload;
    },
    setAttack: (state, action) => {
      state.attack = action.payload;
    },
    setDecay: (state, action) => {
      state.decay = action.payload;
    },
    setSustain: (state, action) => {
      state.sustain = action.payload;
    },
    setRelease: (state, action) => {
      state.release = action.payload;
    },
    setTremoloFrequency: (state, action) => {
      state.tremoloFrequency = action.payload;
    },
    setTremoloDepth: (state, action) => {
      state.tremoloDepth = action.payload;
    },
    setDelayTime: (state, action) => {
      state.delayTime = action.payload;
    },
    setFeedback: (state, action) => {
      state.feedback = action.payload;
    },
    setDistortion: (state, action) => {
      state.distortion = action.payload;
    },
    setBits: (state, action) => {
      state.bits = action.payload;
    },
    setPitchShift: (state, action) => {
      state.pitchShift = action.payload;
    },
    setWave: (state, action) => {
      state.wave = action.payload;
    },
  },
});
export const {
  setVolume,
  setAttack,
  setDecay,
  setSustain,
  setRelease,
  setTremoloFrequency,
  setTremoloDepth,
  setDelayTime,
  setFeedback,
  setDistortion,
  setBits,
  setPitchShift,
  setWave,
} = soundSettingsSlice.actions;
