import { createSlice } from '@reduxjs/toolkit';

const initialState = {
  melodyArray: {
    "B6": [], "A#6": [], "A6": [], "G#6": [], "G6": [], "F#6": [], "F6": [], "E6": [], "D#6": [], "D6": [], "C#6": [], "C6": [],
    "B5": [], "A#5": [], "A5": [], "G#5": [], "G5": [], "F#5": [], "F5": [], "E5": [], "D#5": [], "D5": [], "C#5": [], "C5": [],
    "B4": [], "A#4": [], "A4": [], "G#4": [], "G4": [], "F#4": [], "F4": [], "E4": [], "D#4": [], "D4": [], "C#4": [], "C4": [],
    "B3": [], "A#3": [], "A3": [], "G#3": [], "G3": [], "F#3": [], "F3": [], "E3": [], "D#3": [], "D3": [], "C#3": [], "C3": [],
    "B2": [], "A#2": [], "A2": [], "G#2": [], "G2": [], "F#2": [], "F2": [], "E2": [], "D#2": [], "D2": [], "C#2": [], "C2": [],
    "B1": [], "A#1": [], "A1": [], "G#1": [], "G1": [], "F#1": [], "F1": [], "E1": [], "D#1": [], "D1": [], "C#1": [], "C1": [],
  }
};

export const melodyArraySlice = createSlice({
  name: 'melodyArray',
  initialState,
  reducers: {
    initArray: (state, action) => {
      var dict = state.melodyArray;
      for(var key in dict) {
        for (var i = 0; i < action.payload * 4; i++) {
          dict[key].push(false);
        }
      }
      state.melodyArray = dict;
    },
    resetArray: (state) => {
      var dict = state.melodyArray;
      for(var key in dict) {
          dict[key] = [];
      }
      state.melodyArray = dict;
    },
    resizeArray: (state, action) => {
      const bits = action.payload * 4;
      var dict = {};
      for(var key in state.melodyArray) {
        dict[key] = [];
        for (var i = 0; i < bits; i++) {
          dict[key].push(state.melodyArray[key][i] || false);
        }
      }
      state.melodyArray = dict;
    },
    changeValue: (state, action) => {
      var dict = state.melodyArray;
      const key = action.payload[0];
      const index = action.payload[1];
      const value = action.payload[2];
      dict[key][index] = value;
      state.melodyArray = dict;
    },
  }
});
export const { initArray, resetArray, resizeArray, changeValue } = melodyArraySlice.actions;
